import {register} from "../../api/user.js"
import navT from "@/components/nav/nav.vue"
import {mapState,mapMutations} from "vuex"

export default {
	components: {navT},
	data() {
		return {
			confirm:"",
			account:'',
			password:''
			
		}
	},
	methods: {
		...mapMutations(["changName"]),
		toLogin(){
			//页面跳转
			uni.redirectTo({
				url: '../login/login',
				
			});
			
		},
		show(val){
			uni.showModal({
				content:val
			})
			
		},
		testPhone(){
			var regex = /^[1][3,4,5,7,8][0-9]{9}$/;
			if(!regex.test(this.account)){
				this.show("请输入正确的手机号")
				return false;
			}else{
				
				return true;
			}
		},
		testPassword(){
			if(this.password.length>=6){
				return true;
			}else{
				this.show("密码格式有问题");
				return false;
				
			}
		},
		testConfirm(){
			if(this.password === this.confirm){
				return true;
			}else{
				this.show("两次输入的密码不一致");
				return false;
			}
		},
		register(){
			if(this.testPhone()&&this.testPassword()&&this.testConfirm()){
				
				var data={
					account:this.account,
					captche:"123456",
					password:this.password,
					spread:null
				};
				register(data).then((data)=>{console.log(data)})
			}else{
				console.log(0);
			}
			
			
			
		}
	},
	computed:{
		btn(){
			if(this.accoun!=''&&this.password!=''&&this.confirm!=''){
				return true
			}else{
				return false
			}
		},
		...mapState(["name"])
	}
}
